import Base from './Base'
import axios from 'axios'
import API from '../../../config/api'
import CONST from '../../../config/const'

class FileData extends Base {
    constructor(component) {
        super(component)
    }

    /**
     * proxy
     * (:)
     */
    do() {
        let proxy = this.component.config.proxy
        if (!proxy || !proxy.dsType || !proxy.dsId) {
            return new Promise((resolve, reject) => {
            })
        }
        // Promise
        return new Promise((resolve, reject) => {
            axios({
                url: API.DATA_SOURCE.QUERY,
                method: CONST.AJAX.POST,
                data: {
                    dsId: proxy.dsId,
                    type: proxy.dsType,
                },
            }).then((response) => {
                resolve(response)
            }).catch((error) => {
                reject(error.data)
            })
        })
    }
}

export default FileData
