<script>
    export default {
        name: 'BaseTable',
        props: ['component', 'state'],
        data() {
            return {
                //
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                tableSize: [
                    {label: '', value: 'medium'},
                    {label: '', value: 'small'},
                    {label: '', value: 'mini'},
                ],
                columnType: [
                    {label: '', value: 'text'},
                    {label: '', value: 'index'},
                ],
                // 
                currentColumn: '0'
            }
        },
        created() {
        },
        computed: {},
        methods: {
            /**
             * 
             */
            addColumn(e) {
                this.component.config.option.columns.push({
                    type: "text",
                    text: "",
                    prop: "",
                    width: 100,
                    fixed: false,
                    sortable: false
                })
                this.currentColumn = this.component.config.option.columns.length - 1 + '';
            },
            /**
             * 
             */
            removeColumn(name) {
                this.component.config.option.columns.splice(name, 1)
                if (this.currentColumn !== name) {
                    return;
                }
                if (this.currentColumn * 1 > 0) {
                    this.currentColumn = this.currentColumn - 1 + ''
                }
            }
        },
        components: {}
    }
</script>
