<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import 'echarts-wordcloud'
    import _ from 'lodash'

    export default {
        name: 'WordCloud',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    series: [{
                        type: 'wordCloud',
                        data: null,
                        shape: 'circle',
                        left: 'center',
                        top: 'center',
                        width: 380,
                        height: 280,
                        right: null,
                        bottom: null,
                        sizeRange: [10, 30],
                        rotationRange: [-45, 90],
                        drawOutOfBound: false,
                        textStyle: {
                            normal: {
                                fontFamily: 'sans-serif',
                                fontWeight: 'bold',
                                color: function () {
                                    return 'rgb(' + [
                                        Math.floor(Math.random() * 255),
                                        Math.floor(Math.random() * 255),
                                        Math.floor(Math.random() * 255)
                                    ].join(',') + ')';
                                }
                            },
                            emphasis: {
                                shadowBlur: 10,
                                shadowColor: '#333'
                            }
                        },
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: "", value: 999},
                    {name: "", value: 888},
                    {name: "", value: 777},
                    {name: "", value: 688},
                    {name: "", value: 588},
                    {name: "", value: 516},
                    {name: "", value: 515},
                    {name: "", value: 483},
                    {name: "", value: 462},
                    {name: "", value: 449},
                    {name: "", value: 429},
                    {name: "", value: 407},
                    {name: "", value: 406},
                    {name: "", value: 406},
                    {name: "", value: 386},
                    {name: "", value: 385},
                    {name: "", value: 375},
                    {name: "", value: 355},
                    {name: "", value: 355},
                    {name: "", value: 335},
                    {name: "", value: 324},
                    {name: "", value: 304},
                    {name: "", value: 304},
                    {name: "", value: 284},
                    {name: "", value: 284},
                    {name: "", value: 284},
                    {name: "", value: 254},
                    {name: "", value: 254},
                    {name: "", value: 253},
                    {name: "", value: 253},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 223},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 152},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 112},
                    {name: "", value: 92}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let data = []
                for (let i = 0; i < this.config.data.length; i++) {
                    data.push({
                        name: this.config.data[i][propName],
                        value: this.config.data[i][propValue]
                    })
                }
                this.config.option.series[0].data = data
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
