<template>
    <el-collapse value="0" accordion>
        <!--  -->
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="1">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.interval"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.autoplay"></el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.type"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in type"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.trigger"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in trigger"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="2">
            <template v-for="(item, i) in component.config.option.data">
                <el-row :key="i">
                    <el-col :span="8" class="tag">{{'' + i}}</el-col>
                    <el-col :span="16">
                        <el-input v-model="component.config.option.data[i].url"
                                  placeholder=""
                                  size="mini" clearable></el-input>
                    </el-col>
                </el-row>
            </template>
            <el-row>
                <el-col :span="16" :offset="8">
                    <el-upload drag
                               class="img-upload"
                               :file-list="component.config.option.data"
                               :headers="getToken()"
                               :action="getUploadUrl()"
                               :before-upload="beforeUpload"
                               :on-success="onSuccessCover"
                               :on-Error="onError"
                               :on-remove="onRemove"
                               :show-file-list="true"
                               multiple>
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em></em></div>
                    </el-upload>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import API from '../../../../config/api'

    export default {
        name: 'Slider',
        props: ['component', 'state'],
        data() {
            return {
                type: [
                    {label: '', value: undefined},
                    {label: '', value: 'card'},
                ],
                trigger: [
                    {label: '', value: undefined},
                    {label: '', value: 'click'},
                ]
            }
        },
        methods: {
            /**
             * token
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg' && ext !== 'gif') {
                    this.$message({
                        message: "jpgpngbmp",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * 
             */
            onSuccessCover(response, file, fileList) {
                this.component.config.option.data.push({
                    name: file.name,
                    url: API.ATTACHMENT.DOWNLOAD + "?id=" + response.data.id
                })
            },
            /**
             * 
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 
             */
            onRemove(file, fileList) {
                let data = []
                if (fileList && fileList.length) {
                    for (let i = 0; i < fileList.length; i++) {
                        let item = fileList[i]
                        data.push({
                            name: item.name,
                            url: item.url
                        })
                    }
                }
                this.component.config.option.data = data
            }
        }
    }
</script>
