<template>
    <el-collapse value="0" accordion>
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <el-collapse-item title="" name="1">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.vertical">
                        {{component.config.option.vertical ? "" : ""}}
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.vertical">
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.progressHeight"
                                     size="mini"
                                     controls-position="right" :min="0" :step="1">
                    </el-input-number>
                </el-col>
            </el-row>
            <el-row v-else>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.progressWidth"
                                     size="mini"
                                     controls-position="right" :min="0" :step="1">
                    </el-input-number>
                </el-col>
            </el-row>
        </el-collapse-item>
        <el-collapse-item title="" name="2">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.start"
                               placeholder=""
                               size="mini" @change="changeStart">
                        <el-option v-for="item in nodeList"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="4">
                    <el-checkbox v-model="component.config.option.playable"></el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.playable">
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="10">
                    <el-input-number v-model="component.config.option.interval"
                                     size="mini"
                                     controls-position="right" :min="0" :step="1">
                    </el-input-number>
                </el-col>
                <el-col :span="2" class="tag">
                    <el-checkbox v-model="component.config.option.loop"></el-checkbox>
                </el-col>
                <el-col :span="4" class="tag"></el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    export default {
        name: 'Timeline',
        props: ['component', 'state'],
        data() {
            return {
                nodeList: [],
            }
        },
        mounted() {
            this.initNodeList()
        },
        computed: {},
        methods: {
            /**
             * 
             */
            initNodeList() {
                this.nodeList = []
                let data = this.component.config.option.data
                if (!data || !data.length) {
                    return
                }
                for (let i = 0; i < data.length; i++) {
                    this.nodeList.push({
                        label: data[i].name,
                        value: data[i].value,
                    })
                }
            },
            /**
             * 
             * @param val
             */
            changeStart(val) {
                this.component.config.option.value = val
            }
        },
        watch: {
            "component.config.option.data": {
                deep: true,
                handler: function () {
                    this.initNodeList()
                }
            }
        },
        components: {}
    }
</script>
