<template>
    <Table :config="config" :base="base"></Table>
</template>

<script>
    import Table from '@/components/screen/layer/base/Table'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'BaseTable',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initOption()
            this.initData()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        mounted() {

        },
        beforeDestroy() {
            // 
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        methods: {
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    top: 30,
                    left: 30,
                    height:200,
                    opacity: 1,
                    display: 'block',
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderWidth: 2,
                    borderColor: '#00BAFF',
                    header: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: '#151a23',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 0,
                        paddingTop: 10,
                    },
                    body: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: '#151a23',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 10,
                        paddingTop: 10,
                    }
                }, this.config.style)
                this.config.style = style
            },
            /**
             * option
             */
            initOption() {
                this.config.option = _.assignIn({}, {
                    border: true,
                    stripe: true,
                    fit: false,
                    size: "medium",
                    maxHeight: null,
                    highlightCurrentRow: true,
                    showSummary: false,
                    sumText: '',
                    columns: [{
                        type: 'text', text: '', prop: 'name', width: 100, fixed: false, sortable: true
                    }, {
                        type: 'text', text: '', prop: 'value', width: 100, fixed: false, sortable: true
                    }]
                }, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: "", value: 300},
                    {name: "", value: 200},
                    {name: "", value: 100},
                    {name: "", value: 300},
                    {name: "", value: 200},
                    {name: "", value: 100},
                    {name: "", value: 300},
                    {name: "", value: 200},
                    {name: "", value: 100},
                    {name: "", value: 300},
                    {name: "", value: 200},
                    {name: "", value: 100},
                    {name: "", value: 300},
                    {name: "", value: 200},
                    {name: "", value: 100}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'cellClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'rowClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'headerClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    },
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: null
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
            },
            /**
             * 
             */
            onChange(val) {
                if (!this.config.option.data || !this.config.option.data.length) {
                    return
                }
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.option.data.length; i++) {
                    let item = this.config.option.data[i]
                    if (item[propValue] == val) {
                        this.onEvent("change", item, i)
                    }
                }
            },
            /**
             * 
             */
            onEvent(eventName, item, index) {
                this.$children[0].onEvent(eventName, item, index)
            },
        },
        watch: {},
        components: {
            "Table": Table
        }
    }
</script>
