<template>
    <Map :config="config" :base="base"></Map>
</template>

<script>
    import Map from '@/components/screen/layer/base/Map'
    // import ChinaCity from '../../../map/coordinate/china-city'
    import _ from 'lodash'

    export default {
        name: 'ColorMap',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    visualMap: {
                        show: true,
                        orient: 'vertical',
                        min: 0,
                        max: 800,
                        calculable: true,
                        color: ['#4FA8F9', '#FFFFFF'],
                        left: 'left',
                        itemWidth: 20,
                        itemHeight: 100,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    series: [{
                        type: 'map',
                        mapType: 'china',
                        zlevel: 2,
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
                this.config.areaSource='';
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: "", value: 852},
                    {name: "", value: 574},
                    {name: "", value: 682},
                    {name: "", value: 432},
                    {name: "", value: 188},
                    {name: "", value: 315},
                    {name: "", value: 596},
                    {name: "", value: 494},
                    {name: "", value: 4},
                    {name: "", value: 539},
                    {name: "", value: 342},
                    {name: "", value: 514},
                    {name: "", value: 366},
                    {name: "", value: 257},
                    {name: "", value: 740},
                    {name: "", value: 350},
                    {name: "", value: 278},
                    {name: "", value: 557},
                    {name: "", value: 417},
                    {name: "", value: 363},
                    {name: "", value: 175},
                    {name: "", value: 927},
                    {name: "", value: 431},
                    {name: "", value: 387},
                    {name: "", value: 351},
                    {name: "", value: 157},
                    {name: "", value: 609},
                    {name: "", value: 819},
                    {name: "", value: 791},
                    {name: "", value: 346},
                    {name: "", value: 462},
                    {name: "", value: 367},
                    {name: "", value: 533},
                    {name: "", value: 935}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '',
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    },
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = []
                let propName = this.config.transform.mapper.name || 'name'
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    let name = item[propName]
                    let value = item[propValue]
                    data.push({
                        name: name,
                        value: value
                    })
                }
                this.config.option.series[0].data = data
            }
        },
        components: {
            'Map': Map
        },
    }
</script>
