<template>
    <el-collapse value="0" accordion>
        <!--  -->
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="1">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.backgroundColor"
                                     size="mini"
                                     show-alpha></el-color-picker>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.series[0].orient"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in orient"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.series[0].focusNodeAdjacency">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="7">
                    <el-select v-model="component.config.option.series[0].label.normal.position"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in label.positions"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                    <div class="description"></div>
                </el-col>
                <el-col :span="7" :offset="1">
                    <el-input-number v-model="component.config.option.series[0].label.normal.rotate"
                                     size="mini"
                                     controls-position="right"
                                     :min="0"
                                     :max="360">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="7" :offset="8">
                    <el-select v-model="component.config.option.series[0].label.normal.align"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in label.align"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                    <div class="description"></div>
                </el-col>
                <el-col :span="7" :offset="1">
                    <el-select v-model="component.config.option.series[0].label.normal.verticalAlign"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in label.verticalAlign"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" :offset="8">
                    <el-input-number v-model="component.config.option.series[0].label.normal.textStyle.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.series[0].label.normal.textStyle.color"
                                     size="mini"
                                     show-alpha></el-color-picker>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.series[0].lineStyle.normal.color"
                                     size="mini"
                                     show-alpha></el-color-picker>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="addThemeColor">
                    </el-button>
                </el-col>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="minusThemeColor">
                    </el-button>
                </el-col>
            </el-row>
            <el-row>
                <template v-for="(color, i) in component.config.option.color">
                    <el-col :key="i" :span="3">
                        <el-color-picker v-model="component.config.option.color[i]"
                                         size="mini"
                                         show-alpha></el-color-picker>
                    </el-col>
                </template>
            </el-row>
        </el-collapse-item>

        <!--  -->
        <el-collapse-item title="" name="2">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.tooltip.show">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.textStyle.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.tooltip.textStyle.color"
                                     size="mini"
                                     show-alpha></el-color-picker>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.borderWidth"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.tooltip.borderColor"
                                     size="mini"
                                     show-alpha></el-color-picker>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.padding[0]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                    <el-input-number v-model="component.config.option.tooltip.padding[3]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.padding[2]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                    <el-input-number v-model="component.config.option.tooltip.padding[1]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseSankey',
        props: ['component', 'state'],
        data() {
            return {
                // 
                orient: [
                    {label: '', value: 'horizontal'},
                    {label: '', value: 'vertical'},
                ],
                // 
                label: {
                    positions: [
                        {label: "", value: "top"},
                        {label: "", value: "left"},
                        {label: "", value: "right"},
                        {label: "", value: "bottom"},
                        {label: "", value: "inside"},
                        {label: "", value: "insideLeft"},
                        {label: "", value: "insideRight"},
                        {label: "", value: "insideTop"},
                        {label: "", value: "insideBottom"},
                        {label: "", value: "insideTopLeft"},
                        {label: "", value: "insideBottomLeft"},
                        {label: "", value: "insideTopRight"},
                        {label: "", value: "insideBottomRight"},
                    ],
                    verticalAlign: [
                        {label: "", value: "top"},
                        {label: "", value: "middle"},
                        {label: "", value: "bottom"},
                    ],
                    align: [
                        {label: "", value: "left"},
                        {label: "", value: "center"},
                        {label: "", value: "right"},
                    ]
                }
            }
        },
        methods: {
            /**
             * 
             */
            addThemeColor() {
                this.component.config.option.color.push(ColorUtils.randomHex())
            },
            /**
             * 
             */
            minusThemeColor() {
                this.component.config.option.color.pop()
            },
        },
    }
</script>
