<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseFunnel',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        trigger: 'item',
                        formatter: "{b} : {c} "
                    },
                    color: ["#FFDB5C", "#9FE6B8", "#67E0E3", "#32C5E9", "#37A2DA"],
                    series: [{
                        name: '',
                        type: 'funnel',
                        x: '10%',
                        width: '80%',
                        min: 0,
                        max: 100,
                        minSize: 0,
                        maxSize: '100%',
                        sort: 'descending',
                        funnelAlign: 'center',
                        gap: 0,
                        data: null,
                        label: {
                            show: true,
                            formatter: '{b} {c}',
                            position: 'center'
                        },
                        itemStyle: {
                            normal: {
                                borderWidth: 0,
                                shadowBlur: 30,
                                shadowOffsetX: 0,
                                shadowOffsetY: 10,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {value: 60, name: ''},
                    {value: 40, name: ''},
                    {value: 20, name: ''},
                    {value: 80, name: ''},
                    {value: 100, name: ''}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propX = mapper.name || 'name'
                let propY = mapper.value || 'value'
                let series = []
                for (let i = 0; i < data.length; i++) {
                    series.push({
                        name: data[i][propX],
                        value: data[i][propY],
                    })
                }
                this.config.option.series[0].data = series.sort(function (a, b) {
                    return a.value - b.value
                })
                this.config.option.series[0].max = series[series.length - 1].value
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
