    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseScatter',
        props: ['component', 'state'],
        data() {
            return {
                //
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ],
                    legendX:0
                },
                seriesOption: []
            }
        },
        created() {
            this.initSeriesOption();
            this.init()
        },
        computed: {},
        methods: {
            init(){
                this.legend.legendX=parseInt(this.component.config.option.legend.left);
            },
            /**
             * 
             */
            changeLengXPositon(val){
                this.component.config.option.legend.left=val+'%';
            },
            /**
             * 
             */
            initSeriesOption() {
                let opt = []
                if (this.component.config.option.dataset && this.component.config.option.dataset.length) {
                    for (let i = 0; i < this.component.config.option.dataset.length; i++) {
                        let model = this.component.config.option.dataset[i]['source']
                            && this.component.config.option.dataset[i]['source'][0]
                        if (model[4]) {
                            opt.push({label: model[4], value: i})
                        }
                    }
                }
                this.seriesOption = opt
            },
            /**
             * 
             */
            setOutOfRangeSize() {
                this.component.config.option.visualMap[0].outOfRange.symbolSize =
                    this.component.config.option.visualMap[0].inRange.symbolSize
            },
            /**
             * 
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper this.$set() 
                // mappersetterdom
                let seriesName = '' + index
                this.component.config.option.legend.data.push(seriesName)
                this.component.config.option.color.push(ColorUtils.randomHex())
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'scatter',
                    itemStyle: {
                        normal: {
                            opacity: 0.8,
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowOffsetY: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    },
                    datasetIndex: 0
                })
            },
            /**
             * 
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                this.component.config.option.color.splice(index, 1)
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
            },
            /**
             * series
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
