<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: 'BaseLine',
        props: ['component', 'state'],
        data() {
            return {
                //
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ],
                    legendX:0
                },
                areaColor: []
            }
        },
        created() {
            this.initAreaColor()
            this.init();
        },
        computed: {},
        methods: {
            init(){
                this.legend.legendX=parseInt(this.component.config.option.legend.left);
            },
            /**
             * 
             */
            changeLengXPositon(val){
                this.component.config.option.legend.left=val+'%';
            },
            /**
             * AreaColor
             */
            initAreaColor() {
                this.areaColor = []
                let option = this.component.config.option
                if (!option.series || !option.series.length) {
                    return
                }
                for (let i = 0; i < option.series.length; i++) {
                    let series = option.series[i]
                    this.areaColor.push(series.areaStyle.normal.color)
                }
            },
            /**
             * Area
             */
            setAreaColor(index) {
                this.component.config.option.series[index].areaStyle.normal.color = this.areaColor[index]
            },
            /**
             * 
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper this.$set() 
                // mappersetterdom
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = ''
                this.component.config.option.legend.data.push(seriesName)
                let color = {
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    type: 'linear',
                    global: false,
                    colorStops: [
                        {offset: 0, color: ColorUtils.randomHex()},
                        {offset: 0.5, color: 'rgba(0,0,0,0)'},
                        {offset: 1, color: 'rgba(0,0,0,0)'}
                    ]
                }
                this.areaColor.push(color)

                this.component.config.option.markLines.push({
                    show:true,
                    markLine:{
                        data:[{yAxis:50}],
                        lineStyle:{
                            width:'1',
                            opacity:'1'
                        }
                    }
                });
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'line',
                    smooth: true,
                    showSymbol: false,
                    symbol: 'circle',
                    symbolSize: 6,
                    areaStyle: {
                        normal: {
                            color: color
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: ColorUtils.randomHex()
                        }
                    },
                    lineStyle: {
                        normal: {
                            width: 3
                        }
                    }
                })
            },
            /**
             * 
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // mapper
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.areaColor.splice(index, 1)
            },
            /**
             * series
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            },
            /**
             * 
             */
            changeMarkLineFlag(value, index){
                if(value){
                    this.component.config.option.series[index].markLine=this.component.config.option.markLines[index].markLine;
                }else{
                    this.component.config.option.series[index].markLine=null;
                }
            }
        },
        components: {},
    }
</script>
