<template>
    <el-collapse value="0" accordion>
        <!--  -->
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="1">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.style.backgroundColor"
                                     size="mini" show-alpha></el-color-picker>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="13">
                    <el-input v-model="component.config.style.backgroundImage"
                              size="mini" clearable>
                    </el-input>
                </el-col>
                <el-col :span="3" class="tag">
                    <el-button circle size="mini"
                               class="el-icon-more"
                               style="margin-left: 5px;"
                               @click="openPictureStore"></el-button>
                </el-col>
                <el-col :span="16" :offset="8">
                    <el-upload drag
                               class="img-upload"
                               :headers="getToken()"
                               :action="getUploadUrl()"
                               :before-upload="beforeUpload"
                               :on-success="onSuccessCover"
                               :on-Error="onError"
                               :show-file-list="false">
                        <img v-if="component.config.style.backgroundImage"
                             :src="component.config.style.backgroundImage"
                             class="img-preview">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em></em></div>
                    </el-upload>
                    <div v-if="component.config.style.backgroundImage"
                         class="img-cover">
                        <i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
                    </div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.borderTopLeftRadius"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                    <el-input-number v-model="component.config.style.borderBottomLeftRadius"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.borderTopRightRadius"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                    <el-input-number v-model="component.config.style.borderBottomRightRadius"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.style.borderStyle"
                               placeholder=""
                               size="mini">
                        <el-option v-for="item in borderStyle"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" :offset="8">
                    <el-input-number v-model="component.config.style.borderWidth"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.style.borderColor"
                                     size="mini" show-alpha></el-color-picker>
                </el-col>
                <div class="description"></div>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import API from '../../../../config/api'

    export default {
        name: 'Label',
        props: ['component', 'state'],
        data() {
            return {
                //
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                borderStyle: [
                    {label: '', value: 'none'},
                    {label: '', value: 'solid'},
                    {label: '', value: 'dotted'},
                    {label: '', value: 'dashed'},
                    {label: '', value: 'double'},
                    {label: '3D', value: 'groove'},
                    {label: '3D', value: 'ridge'},
                    {label: '3D', value: 'inset'},
                    {label: '3D', value: 'outset'},
                ]
            }
        },
        mounted() {
        },
        computed: {},
        methods: {
            /**
             * token
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg' && ext !== 'gif') {
                    this.$message({
                        message: "jpgpngbmp",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD_PROPRIETARY
            },
            /**
             * 
             */
            onSuccessCover(res) {
                this.component.config.style.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 
             */
            removeCoverImage() {
                this.component.config.style.backgroundImage = null
            },
            /**
             * 
             */
            openPictureStore() {
                this.state.pictureStore.visible = true
                this.state.pictureStore.item = this.component.config
                this.state.pictureStore.prop = "style.backgroundImage"
            },
        },
        components: {},
    }
</script>
