<template>
    <Map :config="config" :base="base"></Map>
</template>

<script>
    import Map from '@/components/screen/layer/base/Map'
    import ChinaCity from '../../../map/coordinate/china-city'
    import _ from 'lodash'

    export default {
        name: 'FlowMap',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    visualMap: {
                        show: true,
                        min: 0,
                        max: 100,
                        calculable: true,
                        color: ['#ff3333', '#ffff00', '#00ffff'],
                        left: 'left',
                        itemWidth: 20,
                        itemHeight: 100,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    geo: {
                        map: 'china',
                        label: {
                            emphasis: {
                                show: false
                            }
                        },
                        roam: true,
                        layoutCenter: ['50%', '53%'],
                        layoutSize: '108%',
                        itemStyle: {
                            normal: {
                                color: 'rgba(51, 69, 89, .5)',
                                borderColor: 'rgba(100,149,237,1)'
                            },
                            emphasis: {
                                color: 'rgba(37, 43, 61, .5)'
                            }
                        }
                    },
                    series: [{
                        type: 'lines',
                        zlevel: 2,
                        effect: {
                            show: true,
                            period: 4,
                            trailLength: 0.02,
                            symbol: 'circle',
                            symbolSize: 5,
                        },
                        lineStyle: {
                            normal: {
                                width: 1,
                                opacity: 0.6,
                                curveness: 0.2
                            }
                        },
                        data: null
                    }, {
                        type: 'effectScatter',
                        coordinateSystem: 'geo',
                        zlevel: 2,
                        rippleEffect: {
                            period: 4,
                            brushType: 'stroke',
                            scale: 2.5
                        },
                        label: {
                            normal: {
                                show: true,
                                position: 'right',
                                offset: [5, 0],
                                formatter: '{b}'
                            },
                            emphasis: {
                                show: true
                            }
                        },
                        symbol: 'circle',
                        symbolSize: this.calcSymbolSize,
                        itemStyle: {
                            normal: {
                                show: false,
                                color: '#f00'
                            }
                        },
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
				this.config.areaSource='';
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {from: '', value: 100, to: ''},
                    {from: '', value: 70, to: ''},
                    {from: '', value: 30, to: ''},
                    {from: '', value: 50, to: ''},
                    {from: '', value: 20, to: ''},
                    {from: '', value: 10, to: ''},
                    {from: '', value: 80, to: ''},
                    {from: '', value: 55, to: ''},
                    {from: '', value: 90, to: ''}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '',
                    filter: function (params) {
                        if (params.componentType === "series" && params.seriesType === "effectScatter") {
                            return true
                        } else {
                            return false;
                        }
                    },
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                        {name: 'longitude', comment: '', prop: 'value[0]', state: null},
                        {name: 'latitude', comment: '', prop: 'value[1]', state: null},
                        {name: 'value', comment: '', prop: 'value[2]', state: null},
                    ]
                }, {
                    name: 'click',
                    comment: '',
                    filter: function (params) {
                        if (params.componentType === "geo") {
                            return true
                        } else {
                            return false;
                        }
                    },
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        from: null,
                        to: null,
                        value: null
                    },
                    // 
                    variable: {
                        symbolSize: 1
                    }
                }
            },
            /**
             * 
             */
            calcSymbolSize(val) {
                return 4 + val[2] * this.config.transform.variable.symbolSize / 10
            },
            /**
             * config.dataoption
             */
            handler() {
                let lineData = []
                let scatterData = []
                let propFrom = this.config.transform.mapper.from || 'from'
                let propTo = this.config.transform.mapper.to || 'to'
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    let startCity = item[propFrom]
                    let endCity = item[propTo]
                    let value = item[propValue]
                    let startCoord = ChinaCity.get(startCity)
                    let endCoord = ChinaCity.get(endCity)
                    if (startCoord && endCoord) {
                        lineData.push([{
                            coord: startCoord,
                            value: value
                        }, {
                            coord: endCoord,
                        }])
                        scatterData.push({
                            name: startCity,
                            value: startCoord.concat(value)
                        })
                    }
                }
                this.config.option.series[0].data = lineData
                this.config.option.series[1].data = scatterData
            }
        },
        components: {
            'Map': Map
        },
    }
</script>
