<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BasePie',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        trigger: 'item',
                        formatter: "{b} : {c} "
                    },
                    legend: {
                        orient: 'horizontal',
                        icon: 'circle',
                        top: 'bottom',
                        left: 'center',
                        textStyle: {
                            color: '#fff',
                            fontSize: 12
                        },
                    },
                    series: [{
                        type: 'pie',
                        roseType: false,
                        radius: ['40%', '50%'],
                        center: ['50%', '50%'],
                        color: ['#0E7CE2', '#FF8352', '#E271DE', '#F8456B', '#00FFFF', '#4AEAB0'],
                        data: null,
                        labelLine: {
                            normal: {
                                show: true,
                                length: 20,
                                length2: 20,
                                lineStyle: {
                                    color: '#12EABE',
                                    width: 2
                                }
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                formatter: '{c|{c}}\n{hr|}\n{d|{d}%}',
                                rich: {
                                    b: {
                                        fontSize: 14,
                                        color: '#12EABE',
                                        align: 'left',
                                        padding: 4
                                    },
                                    hr: {
                                        borderColor: '#12EABE',
                                        width: '100%',
                                        borderWidth: 2,
                                        height: 0
                                    },
                                    d: {
                                        fontSize: 14,
                                        color: '#fff',
                                        align: 'left',
                                        padding: 4
                                    },
                                    c: {
                                        fontSize: 14,
                                        color: '#fff',
                                        align: 'left',
                                        padding: 4
                                    }
                                }
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '', value: 80},
                    {name: '', value: 87},
                    {name: '', value: 51},
                    {name: '', value: 81},
                    {name: '', value: 23},
                    {name: '', value: 45},
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propX = mapper.name || 'name'
                let propY = mapper.value || 'value'
                let series = []
                for (let i = 0; i < data.length; i++) {
                    series.push({
                        name: data[i][propX],
                        value: data[i][propY],
                    })
                }
                this.config.option.series[0].data = series
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
