                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>

        <!--  -->
        <el-collapse-item title="" name="1">
            <el-collapse value="0">
                <el-collapse-item title="" name="0">
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.startAngle"
                                             size="mini"
                                             controls-position="right" :min="0" :max="360" :step="1">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.radar.shape"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in radarShape"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.radius"
                                             size="mini"
                                             controls-position="right" :min="0">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.splitNumber"
                                             size="mini"
                                             controls-position="right" :min="0">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.name.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.radar.name.show">
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.name.fontSize"
                                             size="mini"
                                             controls-position="right" :min="12">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.radar.name.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.splitArea.show">
                            </el-checkbox>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.splitArea.show">
                            <el-color-picker v-model="component.config.option.radar.splitArea.areaStyle.color[0]"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.splitLine.show">
                            </el-checkbox>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.splitLine.show">
                            <el-color-picker v-model="component.config.option.radar.splitLine.lineStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.axisLine.show">
                            </el-checkbox>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.axisLine.show">
                            <el-color-picker v-model="component.config.option.radar.axisLine.lineStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="" name="1">
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.legend.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.orient"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in legend.orient"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.left"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in legend.alignHorizontal"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.top"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in legend.alignVertical"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.legend.textStyle.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.legend.textStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="" name="2">
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.backgroundColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.textStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[0]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description"></div>
                            <el-input-number v-model="component.config.option.tooltip.padding[3]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[2]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description"></div>
                            <el-input-number v-model="component.config.option.tooltip.padding[1]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!--  -->
        <el-collapse-item title="" name="2">
            <el-row>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="addSeries">
                    </el-button>
                </el-col>
            </el-row>
            <el-collapse value="0">
                <template v-for="(series, i) in component.config.option.series">
                    <el-collapse-item :key="i" :title="series.name" :name="i + ''">
                        <el-row v-if="i > 0">
                            <el-col :span="4">
                                <el-button type="text"
                                           size="mini"
                                           @click="removeSeries(i)">
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag"></el-col>
                            <el-col :span="11">
                                <el-input v-model="component.config.option.series[i].name"
                                          size="mini"
                                          controls-position="right"
                                          @change="changeSeriesName">
                                </el-input>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag"></el-col>
                            <el-col :span="8">
                                <el-select v-model="component.config.option.series[i].lineStyle.normal.type"
                                           placeholder=""
                                           size="mini">
                                    <el-option v-for="item in lineType"
                                               :key="item.value"
                                               :label="item.label"
                                               :value="item.value">
                                    </el-option>
                                </el-select>
                                <div class="description"></div>
                            </el-col>
                            <el-col :span="8">
                                <el-color-picker v-model="component.config.option.series[i].lineStyle.normal.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description"></div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" :offset="8">
                                <el-input-number v-model="component.config.option.series[i].lineStyle.normal.width"
                                                 size="mini"
                                                 controls-position="right" :min="0">
                                </el-input-number>
                                <div class="description"></div>
                            </el-col>
                            <el-col :span="8">
                                <el-color-picker v-model="component.config.option.series[i].areaStyle.normal.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description"></div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag"></el-col>
                            <el-col :span="6">
                                <el-input-number v-model="component.config.option.series[i].itemStyle.borderWidth"
                                                 size="mini"
                                                 controls-position="right" :min="0">
                                </el-input-number>
                                <div class="description"></div>
                            </el-col>
                            <el-col :span="4" :offset="2">
                                <el-color-picker v-model="component.config.option.series[i].itemStyle.borderColor"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description"></div>
                            </el-col>
                            <el-col :span="4">
                                <el-color-picker v-model="component.config.option.series[i].itemStyle.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description"></div>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseScatter',
        props: ['component', 'state'],
        data() {
            return {
                legend: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ]
                },
                radarShape: [
                    {label: '', value: 'polygon'},
                    {label: '', value: 'circle'},
                ],
                lineType: [
                    {label: '', value: 'solid'},
                    {label: '', value: 'dashed'},
                    {label: '', value: 'dotted'},
                ],
            }
        },
        methods: {
            /**
             * 
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper this.$set() 
                // mappersetterdom
                let seriesName = '' + index
                this.component.config.option.legend.data.push(seriesName)
                let color = ColorUtils.randomHex()
                this.component.config.option.series.push({
                    name: seriesName,
                    type: "radar",
                    symbol: "circle",
                    symbolSize: 10,
                    areaStyle: {
                        normal: {
                            color: color
                        }
                    },
                    itemStyle: {
                        color: color,
                        borderColor: color,
                        borderWidth: 10,
                    },
                    lineStyle: {
                        normal: {
                            type: "dashed",

                            color: color,
                            width: 2
                        }
                    },
                    data: null
                })
            },
            /**
             * 
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
            },
            /**
             * series
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
