<template>
    <Map :config="config" :base="base"></Map>
</template>

<script>
    import Map from '@/components/screen/layer/base/Map'
    import ChinaCity from '../../../map/coordinate/china-city'
    import _ from 'lodash'

    export default {
        name: 'HeatMap',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    visualMap: {
                        show: true,
                        min: 0,
                        max: 100,
                        calculable: true,
                        color: ['#ff3333', '#ffff00', '#00ffff'],
                        left: 'left',
                        itemWidth: 20,
                        itemHeight: 100,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    geo: {
                        map: 'china',
                        label: {
                            emphasis: {
                                show: false
                            }
                        },
                        roam: true,
                        layoutCenter: ['50%', '53%'],
                        layoutSize: '108%',
                        itemStyle: {
                            normal: {
                                color: 'rgba(51, 69, 89, .5)',
                                borderColor: 'rgba(100,149,237,1)'
                            },
                            emphasis: {
                                color: 'rgba(37, 43, 61, .5)'
                            }
                        }
                    },
                    series: [{
                        type: 'heatmap',
                        coordinateSystem: 'geo',
                        zlevel: 2,
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
				this.config.areaSource='';
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '', value: 120},
                    {name: '', value: 100},
                    {name: '', value: 70},
                    {name: '', value: 30},
                    {name: '', value: 50},
                    {name: '', value: 20},
                    {name: '', value: 10},
                    {name: '', value: 80},
                    {name: '', value: 55},
                    {name: '', value: 90}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '',
                    filter: function (params) {
                        if (params.componentType === "geo") {
                            return true
                        } else {
                            return false;
                        }
                    },
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    },
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let scatterData = []
                let propName = this.config.transform.mapper.name || 'name'
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    let name = item[propName]
                    let value = item[propValue]
                    let cityCoord = ChinaCity.get(name)
                    scatterData.push({
                        name: name,
                        value: cityCoord.concat(value)
                    })
                }
                this.config.option.series[0].data = scatterData
            }
        },
        components: {
            'Map': Map
        },
    }
</script>
