<template>
    <Plugin :config="config" :base="base">
        <template v-if="config.option.type==='date'">
            <el-date-picker :style="calcStyle"
                            v-model="config.option.value"
                            type="date"
                            placeholder=""
                            format="yyyy  MM  dd "
                            value-format="yyyy-MM-dd"
                            @change="onChange">
            </el-date-picker>
        </template>
        <template v-else>
            <el-date-picker :style="calcStyle"
                            v-model="config.option.value"
                            type="datetime"
                            placeholder=""
                            format="yyyy-MM-dd hh:mm:ss"
                            value-format="yyyy-MM-dd hh:mm:ss"
                            @change="onChange">
            </el-date-picker>
        </template>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'DateTimePicker',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        computed: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width - 5 + 'px',
                    height: this.config.style.height - 5 + 'px',
                    lineHeight: this.config.style.height - 5 + 'px',
                    opacity: this.config.style.opacity,
                    backgroundColor: this.config.style.backgroundColor,
                    color: this.config.style.color,
                    fontSize: this.config.style.fontSize + 'px',
                    fontWeight: this.config.style.fontWeight,
                    borderStyle: 'solid',
                    borderColor: this.config.style.borderColor,
                    borderWidth: this.config.style.borderWidth + 'px',
                    borderTopLeftRadius: this.config.style.borderTopLeftRadius + 'px',
                    borderTopRightRadius: this.config.style.borderTopRightRadius + 'px',
                    borderBottomLeftRadius: this.config.style.borderBottomLeftRadius + 'px',
                    borderBottomRightRadius: this.config.style.borderBottomRightRadius + 'px',
                }
            },
        },
        methods: {
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    height: 50,
                    width: 200,
                    top: 30,
                    left: 30,
                    opacity: 1,
                    color: '#efefef',
                    fontSize: 14,
                    fontWeight: 400,
                    borderColor: '#00BAFF',
                    borderWidth: 2,
                    backgroundColor: '#151a23',
                    borderTopLeftRadius: 4,
                    borderTopRightRadius: 4,
                    borderBottomLeftRadius: 4,
                    borderBottomRightRadius: 4,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {value: null},
                ]
            },
            /**
             * option
             */
            initOption() {
                this.config.option = this.config.option || {
                    value: null,
                    type: 'date',
                    date: null
                }
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'change',
                        comment: '',
                        binders: [
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        value: null,
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
            },
            /**
             * 
             */
            onChange(value) {
                this.onEvent('change', {value: value}, 0)
            },
            /**
             * 
             */
            onEvent(eventName, item, index) {
                this.$children[0].onEvent(eventName, item, index)
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
