<template>
    <Plugin :config="config" :base="base">
        <el-carousel :interval="config.option.interval * 1000"
                     :type="config.option.type || undefined"
                     :trigger="config.option.trigger || undefined"
                     :autoplay="config.option.autoplay"
                     :height="config.style.height + 'px'">
            <el-carousel-item v-for="(item, i) in config.option.data" :key="i">
                <div class="image" :style="calcImage(item)"></div>
            </el-carousel-item>
        </el-carousel>
    </Plugin>
</template>

<script>
    import '../../../../assets/styles/common/paint/slider.scss'
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Slider',
        props: ['config', 'base'],
        data() {
            return {
                // 
                currentIndex: 0
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 
             */
            calcImage(item) {
                return {backgroundImage: 'url(' + item.url + ')' || '',}
            },
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 320,
                    height: 200,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 
             */
            initData() {
                this.config.data = null
            },
            /**
             * option
             */
            initOption() {
                this.config.option = this.config.option || {
                    interval: 4,
                    type: undefined,
                    trigger: undefined,
                    autoplay: true,
                    data: [
                        {name: '1.jpg', url: './img/sample/slider/1.jpg'},
                        {name: '2.jpg', url: './img/sample/slider/2.jpg'},
                        {name: '3.jpg', url: './img/sample/slider/3.jpg'},
                    ]
                }
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = null
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = null
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = null
            },
            /**
             * config.dataoption
             */
            handler() {
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
