<template>
    <el-collapse value="0" accordion>
        <!--  -->
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.iframeHeight"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="16">
                    <el-switch :value="modalState()"
                               active-text=""
                               inactive-text=""
                               @change="toggleModal($event)">
                    </el-switch>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="1">
            <el-row>
                <el-col :span="4" class="tag"></el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.header.show">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.header.show">
                <el-col :span="4">
                    <div class="tag"></div>
                </el-col>
                <el-col :span="20">
                    <el-input v-model="component.config.option.header.title" size="mini"></el-input>
                </el-col>
            </el-row>
        </el-collapse-item>
        <el-collapse-item title="IFRAME" name="2">
            <el-row>
                <el-col :span="4">
                    <div class="tag">URL</div>
                    <el-col :span="4">
                        <div class="tag">URL</div>
                        <el-tooltip placement="left">
                            <div slot="content">
                                URL<br>
                                http://www.tyrival.com/:{abc} :{abc} abc<br>
                                abcURL:{abc}abc
                            </div>
                            <i class="icon iconfont icon-help"></i>
                        </el-tooltip>
                    </el-col>
                </el-col>
                <el-col :span="20">
                    <el-input type="textarea"
                              :rows="12"
                              resize="none"
                              v-model="component.config.trigger[1].binder"
                              size="mini"></el-input>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    export default {
        name: 'BaseModal',
        props: ['component', 'state'],
        data() {
            return {
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
            }
        },
        created() {
        },
        computed: {},
        methods: {
            modalState() {
                return this.component.config.style.visibility == 'visible'
            },
            toggleModal(e) {
                this.component.config.style.visibility = e ? 'visible' : 'hidden'
            },
        },
        components: {},
    }
</script>
