<template>
    <div :id="config.id"
         :style="calcStyle()"
         class="vi-modal">
        <div v-if="config.option.header.show"
             class="vi-modal-header">
            <div class="vi-modal-title">{{config.option.header.title}}</div>
        </div>
        <div class="vi-modal-close" @click="closeModal()">
            <i class="el-icon el-icon-close"></i>
        </div>
        <div class="vi-modal-body">
            <slot name="body"></slot>
        </div>
        <span v-if="config.option.footer.show" class="vi-modal-footer">
            <slot name="footer"></slot>
        </span>
    </div>
</template>

<script>
    import CONST from "../../../../config/const"
    import TriggerHandler from "../../../../utils/TriggerHandler"
    import _ from "lodash"
    import DataProxy from "../../../proxy/DataProxy";

    export default {
        name: "Modal",
        props: ['config', 'base'],
        data() {
            return {
                // 
                dataProxy: null,
                // 
                proxyThread: null
            }
        },
        created() {
            this.initStyle()
            this.initTrigger()
            this.initProxy()
            this.initTransform()
            // this.initEvent()
        },
        mounted() {
            this.init()
        },
        beforeDestroy() {
            // 
            this.$store.commit(CONST.MUTATIONS.SCREEN.UNREGISTER, {
                comp: this
            })
        },
        methods: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    visibility: this.config.style.visibility,
                }
            },
            /**
             * 
             */
            init() {
                this.instanceProxy()
            },
            /**
             * 
             */
            initStyle() {
                this.config.style = _.assignIn({
                    top: 30,
                    left: 30,
                    width: 200,
                    opacity: 1,
                    visibility: 'hidden'
                }, this.config.style)
            },
            /**
             * 
             */
            initTrigger() {
                this.config.trigger = this.config.trigger || [
                    {
                        show: true,
                        name: '',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: ' true '},
                            {key: false, value: 'hidden', comment: ' false '},
                        ]
                    }
                ]
                this.initTriggerStore()
                this.calcTrigger()
            },
            /**
             * Store
             */
            initTriggerStore() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                let store = {}
                let vuex = this.$store
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i]
                    let binder = trig.binder;
                    if (binder && binder.length) {
                        let keys = binder.match(CONST.REGEX.PLACEHOLDER_EXTERNAL);
                        if (!keys || !keys.length) {
                            continue;
                        }
                        for (let i = 0; i < keys.length; i++) {
                            let storeProp = keys[i].substring(2, keys[i].length - 1)
                            store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp}) || null
                            vuex.commit(CONST.MUTATIONS.REGISTER, {
                                // vuex
                                store: storeProp,
                                // 
                                comp: this,
                                // vuex
                                prop: CONST.INTERACTION.COMPONENT_TRIGGER_STORE + storeProp
                            })
                        }
                    }
                }
                this.config.store.trigger = store
            },
            /**
             * trigger
             */
            calcTrigger() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i];
                    let result = TriggerHandler.do(trig, this.config.data, this.config.store.trigger)
                    _.set(this, trig.prop, result);
                }
            },
            /**
             * 
             */
            initOption() {
                this.config.option = _.assignIn({
                    // 
                    clickToClose: true,
                    // esc
                    escToClose: true,
                    // 
                    showCloseBtn: true,
                    // body
                    body: {
                        // html
                        html: '',
                        // 
                        components: []
                    },
                    // footer
                    footer: {
                        // footer
                        hidden: false,
                        // html
                        html: '',
                        // 
                        components: []
                    }
                }, this.config.option)
            },
            /**
             * 
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 0-API / 1- / 2-,
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * proxy
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'close',
                    comment: '',
                    binders: [
                        {name: 'visibility', comment: '', prop: 'config.style.visibility', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.loadData()
                    })
            },
            /**
             * handlerhandleroption
             * optionhandler
             */
            loadData() {
                if (!this.config.data || !this.config.data.length) {
                    return
                }
                if (!this.$parent || !this.$parent.handler) {
                    return
                }
                this.$parent.handler()
            },
            /**
             * 
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.render()
            },

            /**
             * 
             */
            onEvent(eventName, item, index) {
                if (!this.config.event || !this.config.event.length) {
                    return
                }
                // 
                for (let i = 0; i < this.config.event.length; i++) {
                    let event = this.config.event[i]
                    // 
                    if (event.name != eventName) {
                        continue
                    }
                    // 
                    if (!event.binders || !event.binders.length) {
                        continue
                    }
                    // 
                    for (let j = 0; j < event.binders.length; j++) {
                        let binder = event.binders[j]
                        // 
                        if (!binder.state) {
                            continue
                        }
                        // prop
                        let value
                        // option.data
                        // 
                        if (binder.prop) {
                            value = _.get(item, binder.prop)
                        } else {
                            let dataset = this.config.data
                            value = dataset[index][binder.name]
                        }
                        this.commit(binder.state, value)
                    }
                }
            },
            /**
             * 
             */
            closeModal() {
                this.config.style.visibility = 'hidden'
                this.onEvent("close", this)
            }
        },
        watch: {
            /**
             * 
             */
            "config.trigger": {
                deep: true,
                handler: function () {
                    this.initTriggerStore()
                    this.calcTrigger()
                }
            },
            /**
             * proxy
             */
            'config.proxy': {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * 
             */
            "config.store.trigger": {
                deep: true,
                handler: function () {
                    this.calcTrigger()
                }
            },
            /**
             * 
             */
            "config.store.proxy": {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * 
             */
            'config.data': {
                deep: true,
                handler: function () {
                    this.loadData()
                    this.calcTrigger()
                }
            },
            /**
             * 
             */
            'config.transform': {
                deep: true,
                handler: function () {
                    this.proxyData()
                }
            },
            /**
             * option
             */
            'config.handler': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
        }
    }
</script>
