<template>
    <el-collapse value="0" accordion>
        <!--  -->
        <el-collapse-item title="" name="0">
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag"></el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description"></div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!--  -->
        <el-collapse-item title="" name="1">
            <el-collapse value="0">
                <el-collapse-item title="" name="0">
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.series[0].sort"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in sort"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.series[0].funnelAlign"
                                       placeholder=""
                                       size="mini">
                                <el-option v-for="item in funnelAlign"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].min"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].max"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].minSize"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].gap"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.borderWidth"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.series[0].itemStyle.normal.borderColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowOffsetX"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">x</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowOffsetY"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">y</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" :offset="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowBlur"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description"></div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.series[0].itemStyle.normal.shadowColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description"></div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="" name="1">
                    <el-row>
                        <el-col :span="4">
                            <el-button type="text"
                                       size="mini"
                                       @click="addColor">
                            </el-button>
                        </el-col>
                        <el-col :span="4">
                            <el-button type="text"
                                       size="mini"
                                       @click="minusColor">
                            </el-button>
                        </el-col>
                    </el-row>
                    <el-row>
                        <template v-for="(color, i) in component.config.option.color">
                            <el-col :key="i" :span="3">
                                <el-color-picker v-model="component.config.option.color[i]"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                            </el-col>
                        </template>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="" name="2">
                    <el-row>
                        <el-col :span="8" class="tag"></el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.series[0].label.show"></el-checkbox>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseBar',
        props: ['component', 'state'],
        data() {
            return {
                funnelAlign: [
                    {label: '', value: 'left'},
                    {label: '', value: 'center'},
                    {label: '', value: 'right'},
                ],
                sort: [
                    {label: '', value: 'ascending'},
                    {label: '', value: 'descending'},
                ]
            }
        },
        methods: {
            /**
             * 
             */
            addColor() {
                this.component.config.option.color.push(ColorUtils.randomHex())
            },
            /**
             * 
             */
            minusColor() {
                this.component.config.option.color.pop()
            },
        },
        components: {},
    }
</script>
