                        <el-col :span="7" class="tag"></el-col>
                        <el-col :span="8">
                            <el-color-picker
                                v-model="component.config.option.tooltip.backgroundColor"
                                size="mini"
                                show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag"></el-col>
                        <el-col :span="17">
