                    <el-row>
                        <el-col :span="7" class="tag"></el-col>
                        <el-col :span="17">
                            <el-input-number
                                v-model="component.config.option.tooltip.textStyle.fontSize"
                                size="mini"
                                controls-position="right"
                                :min="12">
