                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>

        </el-collapse-item>

        <!-- x -->
        <el-collapse-item title="x" name="2">
