                        <el-col :span="4">
                            <el-button
                                    @click="resetValue('component.config.option.xAxis[0].axisLabel.color', 'xAxisLabelColor')"
                                    type="text" size="mini">
                            </el-button>
                        </el-col>
                    </el-row>
                </el-collapse-item>
