                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.markLines[index].markLine.lineStyle.opacity"
                                                size="mini"
                                                controls-position="right" :min="0" :max="1" :step="0.05">
                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.markLines[index].markLine.lineStyle.width"
                                                size="mini"
                                                controls-position="right"
                                                :min="0">
                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                            </template>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: "BaseBar",
        props: ["component"],
        data() {
            return {
                default: {
                    // 
                    barColor: null,
                    // 
                    labelColor: null,
                    // x
                    xAxisLabelColor: null,
                    // x
                    xAxisLineColor: null,
                    // y
                    yAxisLabelColor: null,
                    // x
                    yAxisLineColor: null,
                },
                // 
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                // 
                barFill: {
                    single: [],
                    gradient: [],
                },
                // 
                labelPositions: [
                    {label: "", value: "top"},
                    {label: "", value: "left"},
                    {label: "", value: "right"},
                    {label: "", value: "bottom"},
                    {label: "", value: "inside"},
                    {label: "", value: "insideLeft"},
                    {label: "", value: "insideRight"},
                    {label: "", value: "insideTop"},
                    {label: "", value: "insideBottom"},
                    {label: "", value: "insideTopLeft"},
                    {label: "", value: "insideBottomLeft"},
                    {label: "", value: "insideTopRight"},
                    {label: "", value: "insideBottomRight"},
                ],
                //
                fontWeight: [
                    {label: '', value: 'normal'},
                    {label: '', value: 'bold'},
                    {label: '', value: 'lighter'},
                ],
                //tooltip
                tooltipType: {
                    trigger: [
                        {label: '', value: 'item'},
                        {label: '', value: 'axis'}
                    ],
                    axisPointer: [
                        {label: '', value: 'line'},
                        {label: '', value: 'shadow'},
                        {label: '', value: 'cross'},
                        {label: '', value: 'none'}
                    ]
                },
                //
                positionType: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ]
                },
            }
        },
        created() {
            this.init()
        },
        methods: {
            init() {
                // 
                let barColor = this.component.config.option.series.map(ele => {
                    return {color: ele.itemStyle.normal.color, isSingle: ele.itemStyle.normal.isSingle}
                })
                let labelColor = this.component.config.option.series.map(ele => ele.label.normal.textStyle.color ? ele.label.normal.textStyle.color : '#ffc72b')
                this.default = {
                    barColor, labelColor,
                    xAxisLabelColor: this.component.config.option.xAxis[0].axisLabel.color,
                    xAxisLineColor: this.component.config.option.xAxis[0].axisLine.lineStyle.color,
                    yAxisLabelColor: this.component.config.option.yAxis[0].axisLabel.color,
                    yAxisLineColor: this.component.config.option.yAxis[0].axisLine.lineStyle.color,
                }
                let single = []
                let gradient = []
                barColor.forEach(ele => {
                    let color = ''
                    let gradientItem = {
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        type: "linear",
                        global: false,
                        colorStops: [
                            {offset: 0, color: "#199cff"},
                            {offset: 1, color: "#0259aa"}
                        ]
                    }
                    if (ele.isSingle) {
                        color = ele.color
                    } else {
                        gradientItem = ele.color
                    }
                    single.push(color)
                    gradient.push(gradientItem)
                })
                this.barFill = {
                    single, gradient
                }
            },
            /**
             * 0- / 1-
             */
            changeBarFill(value, index) {
                if (!this.component.config.option.series[index].itemStyle.normal.isSingle) {
                    this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.barFill.gradient[index])
                } else {
                    this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.barFill.single[index])
                }
            },
            /**
             * 
             */
            resetBarColor(index) {
                this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.default.barColor[index].color)
                this.$set(this.component.config.option.series[index].itemStyle.normal, 'isSingle', this.default.barColor[index].isSingle)
            },
            /**
             * 
             */
            resetValue(prop, defaultProp) {
                _.set(this, prop, _.get(this.default, defaultProp))
            },
            /**
             * 
             */
            setBarWidth(val,index) {
                this.component.config.option.series[index].barWidth = this.numberToPercent(val)
            },
            /**
             * 
             * @param pct
             * @returns {number}
             */
            percentToNumber(pct) {
                if (!pct) {
                    return 0
                }
                return parseInt(pct.substring(0, pct.length - 1)) / 100
            },
            /**
             * 
             * @param val
             * @returns {string}
             */
            numberToPercent(val) {
                if (!val) {
                    val = 0
                }
                return val * 100 + "%"
            },
            /**
             * series
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            },
            // 
            changeToolTip(val) {
                if (val == 'item') {
                    this.$delete(this.component.config.option.tooltip, 'axisPointer')
                } else if (val == 'axis') {
                    let axisPointer = {
                        type: 'shadow',
                        label: {
                            show: true,
                            backgroundColor: '#0286ff'
                        }
                    }
                    this.$set(this.component.config.option.tooltip, 'axisPointer', axisPointer)
                }
            },
            /**
             * 
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper this.$set() 
                // mappersetterdom
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = `${index}`

                let barColor = ColorUtils.randomHex()
                let labelColor = ColorUtils.randomHex()
                this.barFill.single.push(barColor)
                this.barFill.gradient.push({
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    type: "linear",
                    global: false,
                    colorStops: [
                        {offset: 0, color: "#199cff"},
                        {offset: 1, color: "#0259aa"}
                    ]
                })
                this.default.barColor.push({
                    isSingle: true, color: barColor
                })
                this.default.labelColor.push(labelColor)
                this.component.config.option.markLines.push({
                    show:true,
                    markLine:{
                        data:[{yAxis:50}],
                        lineStyle:{
                            width:'1',
                            opacity:'1'
                        }
                    }
                });
                this.component.config.option.legend.data.push(seriesName)
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'bar',
                    barWidth: '20%',
                    barGap: '40%',
                    xAxisIndex: 0,
                    yAxisIndex: 0,
                    label: {
                        normal: {
                            show: true,
                            position: 'top',
                            textStyle: {
                                color: labelColor,
                                fontSize: 16
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            barBorderRadius: 0,
                            isSingle: true,
                            color: barColor
                        }
                    }
                    // ,
                    // markLine:{
                    //     data:[{yAxis:50}]
                    // }
                })
            },
            /**
             * 
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // mapper
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.barFill.single.splice(index, 1)
                this.barFill.gradient.splice(index, 1)
                this.default.barColor.splice(index, 1)
                this.default.labelColor.splice(index, 1)
            },
            /**
             * 
             */
            changeMarkLineFlag(value, index){
                if(value){
                    this.component.config.option.series[index].markLine=this.component.config.option.markLines[index].markLine;
                }else{
                    this.component.config.option.series[index].markLine=null;
                }
            }
        },
        components: {},
    }
</script>
