<template>
    <div class="screen-design"
         v-loading="loading"
         element-loading-background="rgba(0, 0, 0, 0.7)">
        <Menu :config="config" :state="state"></Menu>
        <Paint :config="config" :state="state"></Paint>
        <Layer :config="config" :state="state"></Layer>
        <Config :config="config" :state="state"></Config>
        <ContextMenu :config="config" :state="state"></ContextMenu>
        <Publisher :config="config" :state="state.publisher"></Publisher>
        <DataSourceEdit v-if="state.addDataSourceModal.visible" :config="state.addDataSourceModal"></DataSourceEdit>
        <DynamicComponentList :config="config" :state="state.dynamicComponent"></DynamicComponentList>
        <DynamicComponentEdit :config="config" :state="state.dynamicComponent"></DynamicComponentEdit>
        <Loader :config="config" :state="state.loader"></Loader>
        <PictureStore :config="config" :state="state.pictureStore"></PictureStore>
        <div class="action">
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="showDynamicComponentModal">
                    <i class="icon iconfont icon-star"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="template">
                    <i class="icon iconfont icon-layer"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="publish">
                    <i class="icon iconfont icon-plane"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="save">
                    <i class="icon iconfont icon-save"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="preview">
                    <i class="icon iconfont icon-view"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="loader">
                    <i class="icon iconfont icon-upload"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="exportConfig">
                    <i class="icon iconfont icon-download"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="help">
                    <i class="icon iconfont icon-help"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right">
                <el-button type="text" @click="exit">
                    <i class="icon iconfont icon-exit"></i>
                </el-button>
            </el-tooltip>
        </div>
        <TemplateList v-if="templateConfig.visible" :templateConfig="templateConfig" :config="config">
        </TemplateList>
    </div>
</template>

<script>
    import '../assets/styles/design/main.scss'
    import Menu from '@/components/screen/Menu';
    import Layer from '@/components/screen/Layer'
    import Config from '@/components/screen/Config'
    import Paint from '@/components/screen/Paint'
    import ContextMenu from '@/components/screen/ContextMenu';
    import Publisher from '@/components/screen/Publisher'
    import Loader from '@/components/screen/Loader'
    import PictureStore from '@/components/screen/PictureStore'
    import DynamicComponentList from '@/components/screen/DynamicComponentList'
    import DynamicComponentEdit from '@/components/screen/DynamicComponentEdit'
    import DataSourceEdit from '@/components/datasource/DataSourceEdit'
    import TemplateList from '@/components/template/TemplateList'
    import API from '../config/api'
    import CONST from '../config/const'
    import _ from 'lodash'


    export default {
        name: 'Design',
        data() {
            return {
                // 
                loading: true,
                // 
                state: null,
                // 
                config: null,
                //
                templateConfig: {
                    //
                    visible: false,
                    //
                    dataSource: {},
                    //
                    isCustomTemplat: true,
                    //
                    isNew: true
                },
            }
        },
        created() {
            this.initScreen()
            // 
            window.addEventListener('click', this.cancelSelectComponent)
        },
        mounted() {
            this.loadDynamicComponent()
        },
        methods: {
            /**
             * url
             */
            initScreen() {
                this.state = {
                    // 
                    select: {},
                    // 
                    index: null,
                    // 
                    menu: {
                        dragMode: false,
                        component: null
                    },
                    // 
                    publisher: {
                        visible: false
                    },
                    // 
                    pictureStore: {
                        visible: false,
                        item: null,
                        prop: null
                    },
                    // 
                    dynamicComponent: {
                        //
                        visible: false,
                        // 
                        list: [],
                        // 
                        editMode: {
                            visible: false,
                            item: null
                        },
                    },
                    // 
                    addDataSourceModal: {
                        dataSource: null,
                        visible: false
                    },
                    // 
                    contextMenu: {
                        show: false,
                        // id
                        refId: null,
                        ids: null,
                        style: {top: 0, left: 0}
                    },
                    // 
                    loader: {visible: false},
                    drag: {
                        id: null,
                        isDragging: false,
                        start: null,
                        end: null,
                    },
                    // 
                    grid: {
                        id: 'grid-' + new Date().getTime(),
                        // 
                        configMode: false,
                        // 
                        row: null,
                        // 
                        dragMode: {
                            mouse: {x: null, y: null},
                            column: null
                        }
                    },
                    // 
                    colorPicker: {
                        enable: false,
                        list: []
                    }
                }
                this.config = {
                    // id
                    id: null,
                    // 
                    name: '',
                    // 
                    cover: null,
                    //  0- / 1-
                    state: 0,
                    // 
                    base: {
                        // 
                        enableGrid: false,
                        size: {width: 1800, height: 1200},
                        backgroundColor: 'rgba(0,0,0,0)',
                        backgroundImage: null,
                        scaleMode: '0',
                        grid: {
                            style: {height: 100},
                            columns: [{
                                id: "col-" + new Date().getTime() + '1',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }, {
                                id: "col-" + new Date().getTime() + '2',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }, {
                                id: "col-" + new Date().getTime() + '3',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }]
                        },
                        // 
                        fullScreen: {
                            id: null,
                            component: null
                        },
                    },
                    // 
                    sequence: [],
                    // 
                    components: {}
                }
                let id = this.$route.query.id
                if (!id) {
                    this.loading = false
                    return
                }
                this.ajax({
                    url: API.SCREEN.GET_BY_ID,
                    params: {id: id},
                }).then(response => {
                    let config = response.data
                    if (!config) {
                        this.$message.error('')
                    }
                    if (typeof config.base == "string") {
                        config.base = JSON.parse(config.base)
                    }
                    if (typeof config.sequence == "string") {
                        config.sequence = JSON.parse(config.sequence)
                    }
                    if (typeof config.components == "string") {
                        config.components = JSON.parse(config.components)
                    }
                    if ("ON" === config.state) {
                        config.state = 1;
                    } else {
                        config.state = 0;
                    }
                    this.config = config
                }).finally(() => {
                    this.loading = false
                })
            },
            /**
             * 
             * @param e
             */
            cancelSelectComponent(e) {
                // 
                this.$set(this.state, "select", {})
                // 
                this.state.grid.configMode = false
                this.state.grid.row = null
            },
            /**
             * 
             */
            save() {
                let config = this.formatConfigParam(this.config)
                config.type = this.$route.query.saveType;
                this.ajax({
                    url: API.SCREEN.SAVE,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    if (response.success) {
                        if (!this.config.id) {
                            this.config.id = response.data.id;
                        }
                        this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                    }
                })
            },
            /**
             * 
             */
            publish() {
                this.state.publisher.visible = true
            },
            /**
             * 
             */
            formatConfigParam(config) {
                let param = _.cloneDeep(config)
                for (let key in param) {
                    let value = param[key]
                    if (_.isObject(value)) {
                        param[key] = JSON.stringify(value)
                    }
                }
                return param
            },
            /**
             * 
             */
            preview() {
                let config = this.formatConfigParam(this.config)
                this.ajax({
                    url: API.SCREEN.PREVIEW,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    let link = this.$router.resolve({
                        path: './presentation',
                        query: {id: response.data.id}
                    })
                    window.open(link.href, '_blank')
                })
            },
            /**
             * 
             */
            loader() {
                this.state.loader.visible = true
            },
            /**
             * 
             */
            exportConfig() {
                let config = _.cloneDeep(this.config)
                config.id = null
                let aTag = document.createElement('a')
                let blob = new Blob([JSON.stringify(config, null, 4)])
                aTag.download = "config-" + new Date().getTime() + ".txt"
                aTag.href = URL.createObjectURL(blob)
                aTag.click()
                URL.revokeObjectURL(blob)
            },
            /**
             * 
             */
            help() {
                window.open("http://www.tyrival.com/books/vision-guide/", "_blank")
            },
            /**
             * 
             */
            exit() {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$router.push('./overview')
                }).catch(() => {

                })
            },
            /**
             * 
             */
            template() {
                let dataSource = this.formatConfigParam(this.config)
                this.templateConfig.visible = true;
                this.templateConfig.dataSource = dataSource;
            },
            /**
             * 
             */
            loadDynamicComponent() {
                this.ajax({
                    url: API.DYNAMIC_COMPONENT.LIST,
                    method: CONST.AJAX.POST,
                    data: {},
                }).then(response => {
                    if (response.success) {
                        this.state.dynamicComponent.list = response.data
                    }
                })
            },
            /**
             * 
             */
            showDynamicComponentModal() {
                this.state.dynamicComponent.visible = true;
            }
        },
        components: {
            'Menu': Menu,
            'Layer': Layer,
            'Config': Config,
            'Paint': Paint,
            'Publisher': Publisher,
            'ContextMenu': ContextMenu,
            'DataSourceEdit': DataSourceEdit,
            'TemplateList': TemplateList,
            'Loader': Loader,
            'PictureStore': PictureStore,
            'DynamicComponentList': DynamicComponentList,
            'DynamicComponentEdit': DynamicComponentEdit
        }
    }
</script>

<style scoped>

</style>
