<template>
    <Table :config="config" :base="base"></Table>
</template>

<script>
    import Table from '@/components/screen/layer/base/Table'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'ScrollTable',
        props: ['config', 'base'],
        data() {
            return {
                thread: null,
                // DOM
                dom: null,
                distance: 0
            }
        },
        created() {
            this.initStyle()
            this.initOption()
            this.initData()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        mounted() {
            this.dom = this.$children[0].$refs[this.config.id].bodyWrapper
        },
        beforeDestroy() {
            // 
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        methods: {
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    class: "scroll-auto",
                    top: 30,
                    left: 30,
                    height: 200,
                    width: 200,
                    opacity: 1,
                    display: 'block',
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderWidth: 2,
                    borderColor: '#00BAFF',
                    header: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: 'rgba(21,26,35,0.5)',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 0,
                        paddingTop: 10,
                    },
                    body: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: 'rgba(21,26,35,0.35)',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 10,
                        paddingTop: 10,
                    }
                }, this.config.style)
                this.config.style = style
            },
            /**
             * option
             */
            initOption() {
                this.config.option = _.assignIn({}, {
                    border: true,
                    stripe: false,
                    fit: false,
                    size: "medium",
                    maxHeight: null,
                    highlightCurrentRow: true,
                    showSummary: false,
                    sumText: '',
                    scroll: {
                        speed: 1
                    },
                    columns: [{
                        type: 'text', text: '', prop: 'name', width: 100, fixed: false, sortable: false
                    }, {
                        type: 'text', text: '', prop: 'value', width: 100, fixed: false, sortable: false
                    }, {
                        type: 'text', text: '', prop: 'no', width: 100, fixed: false, sortable: false
                    }]
                }, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {no: 1, name: "", value: 1000},
                    {no: 2, name: "", value: 900},
                    {no: 3, name: "", value: 800},
                    {no: 4, name: "", value: 700},
                    {no: 5, name: "", value: 600},
                    {no: 6, name: "", value: 500},
                    {no: 7, name: "", value: 400},
                    {no: 8, name: "", value: 300},
                    {no: 9, name: "", value: 200},
                    {no: 10, name: "", value: 100}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'cellClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'rowClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'headerClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    },
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: null
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let propNo = mapper.no || 'no'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                        no: data[i][propNo],
                    })
                }
                this.config.option.data = optionData
                this.play()
            },
            /**
             * 
             */
            onChange(val) {
                if (!this.config.option.data || !this.config.option.data.length) {
                    return
                }
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.option.data.length; i++) {
                    let item = this.config.option.data[i]
                    if (item[propValue] == val) {
                        this.onEvent("change", item, i)
                    }
                }
            },
            /**
             * 
             */
            onEvent(eventName, item, index) {
                this.$children[0].onEvent(eventName, item, index)
            },
            /**
             * 
             */
            play() {
                this.stop();
                this.thread = setInterval(() => {
                    this.distance += this.config.option.scroll.speed / 10
                    if (this.distance >= 1) {
                        this.dom.scrollTop += this.distance
                        this.distance = 0
                        if (this.dom.scrollTop > this.config.style.body.height) {
                            this.shiftData()
                            this.dom.scrollTop = 0
                        }
                    }
                }, 10)
            },
            /**
             * 
             */
            shiftData() {
                let item = this.config.data.shift()
                this.config.data.push(item)
            },
            /**
             * 
             */
            stop() {
                clearInterval(this.thread)
            }
        },
        watch: {
            "config.option.scroll.speed": {
                deep: true,
                handler: function () {
                    this.play()
                }
            }
        },
        components: {
            "Table": Table
        }
    }
</script>