<template>
    <Plugin :config="config" :base="base">
        <div class="scroll-text">
            <transition-group tag="ul" :style="calcStyle()" :name="config.option.type">
                <li v-for="(item,i) in config.option.data"
                    v-show="i == index"
                    :key='i'
                    @click="onEvent('click', item)">
                    <div>{{item.name}}</div>
                </li>
            </transition-group>
        </div>
    </Plugin>
</template>

<script>
    import '../../../../assets/styles/common/paint/scroll-text.scss'
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'ScrollText',
        props: ['config', 'base'],
        data() {
            return {
                // 
                index: 0,
                // 
                thread: null,
                // 
                oldType: 'scroll-up',
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    lineHeight: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    color: this.config.style.color,
                    fontSize: this.config.style.fontSize + 'px',
                }
            },
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 200,
                    height: 40,
                    opacity: 1,
                    color: '#00BAFF',
                    fontSize: 20
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '', value: 0},
                    {name: '', value: 1},
                    {name: '', value: 2},
                ]
            },
            /**
             * option
             */
            initOption() {
                this.config.option = this.config.option || {
                    type: 'scroll-up',
                    delayTime: 2,
                    data: []
                }
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '',
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                        {name: 'value', comment: '', prop: 'value', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
                this.play()
            },
            /**
             * 
             */
            onEvent(eventName, item) {
                this.$children[0].onEvent(eventName, item)
            },
            /**
             * 
             */
            play() {
                this.thread = setInterval(() => {
                    if (this.config.option.type !== this.oldType) {
                        switch (this.config.option.type) {
                            // 
                            case 'scroll-up':
                                this.config.option.delayTime = 2
                                break
                            case 'scroll-up-linear':
                                this.config.option.delayTime = 2
                                break
                            case 'scroll-left':
                                this.config.option.delayTime = 2
                                break
                            case 'scroll-left-linear':
                                this.config.option.delayTime = 4
                                break
                            default:
                                this.config.option.delayTime = 2
                                break
                        }
                        this.oldType = this.config.option.type
                        this.stop()
                        this.play()
                    }
                    this.getText()
                }, this.config.option.delayTime * 1000)
            },
            /**
             * 
             */
            getText() {
                if (!this.config.option.data || !this.config.option.data.length) {
                    return
                }
                if (this.index === this.config.option.data.length - 1) {
                    this.index = 0
                } else {
                    this.index++
                }
            },
            /**
             * 
             */
            stop() {
                clearInterval(this.thread)
                this.index = 0
            }
        },
        destroyed() {
            this.stop()
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
