<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'PictorialBar',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'rgba(0,0,0,0)',
                    dataset: {
                        dimensions: null,
                        source: null,
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        },
                        backgroundColor: '#2F3943',
                        borderColor: 'rgba(0,0,0,0)',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            fontSize: 14,
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)'
                    },
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    legend: {
                        show: true,
                        data: ['', '1'],
                        orient: 'horizontal',
                        top: 'top',
                        left: '0%',
                        itemWidth: 18,
                        itemHeight: 8,
                        textStyle: {
                            fontSize: 12,
                            color: ['#D4DFF5']
                        },
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        min: -1,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            show: true,
                            interval: 0,
                            rotate:0,
                            textStyle: {
                                fontSize: 14,
                                color: "#dddddd",
                            }
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                width: 1,
                                color: ['#737985'],
                                type: 'dashed'
                            }
                        },
                    },
                    yAxis: [{
                        type: 'value',
                        axisLabel: {
                            show: true,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        },
                        min: 0,
                        max: undefined,
                        axisTick: {
                            show: false,
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            },
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                width: 1,
                                color: ['#737985'],
                                type: 'dashed'
                            }
                        },
                    }],
                    series: [{
                        name: '',
                        type: 'pictorialBar',
                        barCategoryGap: '-50%',
                        barGap: "-100%",
                        symbol: 'path://d="M150 0 L75 200 L225 200 Z"',
                        symbolRepeat: false,
                        globalCoord: false,
                        itemStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [{
                                        offset: 0,
                                        color: 'rgba(41, 121, 255, 1)'
                                    }, {
                                        offset: 1,
                                        color: 'rgba(41, 121, 255, 0.1)'
                                    }],
                                },
                            }
                        }
                    }, {
                        name: '1',
                        type: 'pictorialBar',
                        barCategoryGap: '-50%',
                        barGap: "-100%",
                        symbol: 'path://d="M150 0 L75 200 L225 200 Z"',
                        symbolRepeat: false,
                        globalCoord: false,
                        itemStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    type: "linear",
                                    global: false,
                                    colorStops: [{
                                        offset: 0,
                                        color: "#3CE7C9"
                                    }, {
                                        offset: 1,
                                        color: 'rgba(60, 231, 201, 0.1)'
                                    }]
                                },
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '', value: 80, series1: 60, series2: 60},
                    {name: '', value: 87, series1: 55, series2: 60},
                    {name: '', value: 51, series1: 35, series2: 60},
                    {name: '', value: 81, series1: 68, series2: 60},
                    {name: '', value: 23, series1: 84, series2: 60},
                    {name: '', value: 45, series1: 26, series2: 60},
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '0', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                        series1: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let dimensions = []
                for (let key in this.config.transform.mapper) {
                    let dim = this.config.transform.mapper[key] || key
                    if (!this.config.data[0].hasOwnProperty(dim)) {
                        dim = null
                    }
                    dimensions.push(dim)
                }
                this.config.option.dataset = {
                    dimensions: dimensions,
                    source: this.config.data
                }
                this.config.option.xAxis.max = this.config.data.length
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
