<template>
    <div :ref="config.id"
         :style="calcStyle()"
         class="chart-ins">
        <div :id="config.id"
             :style="calcChartSize">
        </div>
        <div class="full-screen icon iconfont icon-full-screen"
             @click=fullScreen></div>
    </div>
</template>

<script>
    import echarts from "echarts"
    import DataProxy from "../../../proxy/DataProxy"
    import CONST from "../../../../config/const"
    import IdGenerator from "../../../../utils/IdGenerator";
    import TriggerHandler from "../../../../utils/TriggerHandler"
    import _ from "lodash"

    export default {
        name: "DynamicChart",
        props: ['config', 'base'],
        data() {
            return {
                // chart
                chart: null,
                // 
                dataProxy: null,
                // 
                proxyThread: null,
                // config.data
                // config.transform
                dataInitCompleted: false
            }
        },
        mounted() {
            let remoteConfig = this.config.remote.config
            let func = new Function('"use strict"; let result = ' + remoteConfig + '; return result')
            this.dynamicConfig = func()
            this.initConfig()
            this.init()
        },
        /**
         * 
         */
        beforeDestroy() {
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 
             */
            calcChartSize() {
                // 100%
                if ((this.base.fullScreen.component && this.base.fullScreen.component.id == this.config.id)
                    || this.config.grid.id) {
                    return {width: '100%', height: '100%',}
                } else {
                    return {
                        width: this.config.style.width + 'px',
                        height: this.config.style.height + 'px',
                    }
                }
            },
        },
        methods: {
            /**
             * 
             */
            calcStyle() {
                if (this.base.fullScreen.component && this.base.fullScreen.component.id == this.config.id) {
                    // 
                    if (this.base.enableGrid && this.config.grid.id) {
                        return {
                            width: '100%',
                            height: '100%',
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                            opacity: 1,
                        }
                    } else {
                        return {
                            width: this.base.size.width + 'px',
                            height: this.base.size.height + 'px',
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                            opacity: 1
                        };
                    }
                } else {
                    // 100%
                    if (this.base.enableGrid && this.config.grid.id) {
                        return {
                            width: '100%',
                            height: this.config.style.height + 'px',
                            opacity: this.config.style.opacity,
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                        };
                    } else {
                        return {
                            width: this.config.style.width + 'px',
                            height: this.config.style.height + 'px',
                            opacity: this.config.style.opacity,
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                        };
                    }
                }
            },
            /**
             * 
             */
            initConfig() {
                // config
                if (!this.config.state.configInit) {
                    this.config.state.configInit = true
                    this.initStyleConfig();
                    this.initOptionConfig();
                    this.initDataConfig();
                    this.initEventConfig();
                    this.initProxyConfig();
                    this.initTransformConfig();
                    this.initTriggerConfig();
                } else {
                    /*
                     
                     configwatch
                     */
                    this.reloadConfig()
                }
            },
            initStyleConfig() {
                this.config.style = _.assignIn({
                    top: 30,
                    left: 30,
                    width: 400,
                    height: 300,
                    opacity: 1,
                    display: 'block',
                    visibility: 'visible'
                }, this.dynamicConfig.style, this.config.style)
            },
            initOptionConfig() {
                if (!this.dynamicConfig.option) {
                    let msg = this.formatErrorMsg("option")
                    throw new Error(msg)
                }
                this.config.option = _.assignIn(this.dynamicConfig.option, this.config.option)
            },
            initDataConfig() {
                if (!this.dynamicConfig.data) {
                    let msg = this.formatErrorMsg("data")
                    throw new Error(msg)
                }
                this.config.data = this.config.data || this.dynamicConfig.data
            },
            initEventConfig() {
                this.config.event = this.config.event || this.dynamicConfig.event || []
            },
            initProxyConfig() {
                this.config.proxy = this.config.proxy || _.assignIn({
                    // 0-API / 1- / 2-,
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [],
                }, this.dynamicConfig.proxy)
            },
            initTransformConfig() {
                if (!this.dynamicConfig.transform) {
                    let msg = this.formatErrorMsg("transform")
                    throw new Error(msg)
                }
                this.config.transform = this.config.transform || this.dynamicConfig.transform
            },
            initTriggerConfig() {
                this.config.trigger = this.config.trigger || this.dynamicConfig.trigger || [
                    {
                        show: true,
                        name: '',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: ' true '},
                            {key: false, value: 'hidden', comment: ' false '},
                        ]
                    }
                ]
            },
            /**
             * config
             */
            reloadConfig() {
                this.config.option = _.cloneDeep(this.config.option)
                this.config.transform = _.cloneDeep(this.config.transform)
                this.config.data = _.cloneDeep(this.config.data)
                this.config.event = _.cloneDeep(this.config.event)
                this.config.proxy = _.cloneDeep(this.config.proxy)
                this.config.trigger = _.cloneDeep(this.config.trigger)
            },
            /**
             * 
             */
            init() {
                this.initTrigger()
                this.initIns()
                this.initEvent()
                this.instanceProxy()
            },
            /**
             * chart
             */
            initIns() {
                let dom = document.getElementById(this.config.id)
                this.chart = echarts.init(dom)
            },
            /**
             * 
             */
            initTrigger() {
                this.initTriggerStore()
                this.calcTrigger()
            },
            /**
             * Store
             */
            initTriggerStore() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                let store = {}
                let vuex = this.$store
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i]
                    let binder = trig.binder;
                    if (binder && binder.length) {
                        let keys = binder.match(CONST.REGEX.PLACEHOLDER_EXTERNAL);
                        if (!keys || !keys.length) {
                            continue;
                        }
                        for (let i = 0; i < keys.length; i++) {
                            let storeProp = keys[i].substring(2, keys[i].length - 1)
                            store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp}) || null
                            vuex.commit(CONST.MUTATIONS.REGISTER, {
                                // vuex
                                store: storeProp,
                                // 
                                comp: this,
                                // vuex
                                prop: CONST.INTERACTION.COMPONENT_TRIGGER_STORE + storeProp
                            })
                        }
                    }
                }
                this.config.store.trigger = store
            },
            /**
             * trigger
             */
            calcTrigger() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i];
                    let result = TriggerHandler.do(trig, this.config.data, this.config.store.trigger)
                    _.set(this, trig.prop, result);
                }
            },
            /**
             * 
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 0-API / 1- / 2-,
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * proxy
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 
             */
            initEvent() {
                if (!this.config.event) {
                    return
                }
                let me = this
                // 
                for (let i = 0; i < me.config.event.length; i++) {
                    let event = me.config.event[i]
                    me.chart.on(event.name, function (params) {
                        // 
                        // 
                        if (!event.binders || !event.binders.length) {
                            return
                        }
                        for (let j = 0; j < event.binders.length; j++) {
                            let binder = event.binders[j]
                            if (!binder.state) {
                                continue
                            }
                            let value
                            if (binder.prop) {
                                value = _.get(params, binder.prop)
                            } else {
                                let dataset = me.config.data
                                value = dataset[params.dataIndex][binder.name]
                            }
                            me.commit(binder.state, value)
                        }
                    })
                }
            },
            /**
             * 
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        // this.loadData()
                    })
            },
            /**
             * handlerhandleroption
             * optionhandler
             */
            loadData() {
                if (!this.config.data) {
                    return
                }
                if (!this.dynamicConfig.handler || !this.dynamicConfig.handler) {
                    return
                }
                this.dynamicConfig.handler(this.config)
            },
            /**
             * chart
             */
            loadOption() {
                try {
                    this.chart.setOption(this.config.option, true)
                } catch (e) {
                }
            },
            /**
             * 
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.render()
            },
            /**
             * chart
             */
            resize() {
                this.chart.resize({
                    height: this.config.style.height,
                    width: this.config.style.width,
                    silent: true
                })
            },
            /**
             * 
             */
            fullScreen() {
                this.base.fullScreen.id = this.config.id
                let config = _.cloneDeep(this.config)
                config.id = IdGenerator.randomFullScreenId()
                this.base.fullScreen.component = config
            },
            formatErrorMsg(errMsg) {
                if (this.dynamicConfig.name) {
                    errMsg = this.dynamicConfig.name + ": " + errMsg
                }
                return errMsg
            }
        },
        watch: {
            "config.style.height": function () {
                this.resize()
            },
            "config.style.width": function () {
                this.resize()
            },
            /**
             * 
             */
            "config.trigger": {
                deep: true,
                handler: function () {
                    this.initTriggerStore()
                    this.calcTrigger()
                }
            },
            /**
             * optionoption
             */
            "config.option": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadOption()
                }
            },
            /**
             * chartoption
             */
            "config.transform": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.proxyData()
                }
            },
            /**
             * datadata
             */
            "config.data": {
                deep: true,
                handler: function () {
                    this.loadData()
                    this.calcTrigger()
                    this.dataInitCompleted = true
                }
            },
            /**
             * proxy
             */
            "config.proxy": {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                    this.dataInitCompleted = true
                }
            },
            /**
             * 
             */
            "config.store.trigger": {
                deep: true,
                handler: function () {
                    this.calcTrigger()
                }
            },
            /**
             * 
             */
            "config.store.proxy": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.render()
                }
            },
            /**
             * optionchartoption
             */
            "config.handler": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadData()
                }
            },
        }
    }
</script>
