<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseRadar',
        props: ['config', 'base'],
        data() {
            return {
                seriesOption: [],
                dataset: []
            }
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        show: true,
                        trigger: "item",
                        backgroundColor: '#2F3943',
                        borderColor: 'transparent',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
                    },
                    legend: {
                        show: true,
                        icon: "circle",
                        top: 'top',
                        left: 'left',
                        orient: "horizontal",
                        textStyle: {
                            fontSize: 14,
                            color: "#fff"
                        },
                        data: [""]
                    },
                    radar: {
                        // 
                        radius: 100,
                        // 
                        startAngle: 90,
                        // 
                        splitNumber: 4,
                        // polygon / circle
                        shape: "circle",
                        // 
                        name: {
                            show: true,
                            fontSize: 14,
                            color: "#dedede",
                            fontWeight: "normal"
                        },
                        // 
                        splitArea: {
                            show: true,
                            areaStyle: {
                                color: ["rgba(0,0,0,0)"]
                            }
                        },
                        // 
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: "#acacac"
                            }
                        },
                        // 
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: "#777777"
                            }
                        },
                        indicator: null
                    },
                    series: [{
                        name: "",
                        type: "radar",
                        symbol: "circle",
                        symbolSize: 10,
                        areaStyle: {
                            normal: {
                                color: "rgba(245, 166, 35, 0.4)"
                            }
                        },
                        itemStyle: {
                            color: 'rgba(245, 166, 35, 1)',
                            borderColor: 'rgba(245, 166, 35, 0.3)',
                            borderWidth: 10,
                        },
                        lineStyle: {
                            normal: {
                                type: "dashed",
                                color: "rgba(245, 166, 35, 1)",
                                width: 2
                            }
                        },
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {series: "", name: "", value: 55, max: 100},
                    {series: "", name: "", value: 67, max: 100},
                    {series: "", name: "", value: 89, max: 100},
                    {series: "", name: "", value: 78, max: 100},
                    {series: "", name: "", value: 78, max: 100},
                    {series: "", name: "", value: 67, max: 100},
                    {series: "", name: "", value: 57, max: 100},
                    {series: "", name: "", value: 85, max: 100},
                    {series: "", name: "", value: 74, max: 100},
                    {series: "", name: "", value: 85, max: 100},
                    {series: "", name: "", value: 81, max: 100},
                    {series: "", name: "", value: 62, max: 100}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'series', comment: '', prop: 'series', state: null},
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                            {name: 'max', comment: '', prop: 'max', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                        series: null,
                        max: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                if (!this.config.data || !this.config.data.length) {
                    return
                }
                let indicat = []
                let max = 0
                let nameProp = this.config.transform.mapper.name || 'name'
                let valueProp = this.config.transform.mapper.value || 'value'
                let seriesProp = this.config.transform.mapper.series || 'series'
                let maxProp = this.config.transform.mapper.max || 'max'
                let dataset = []
                // seriesdataset
                if (this.config.data[0][seriesProp]) {
                    this.seriesOption = []
                    for (let i = 0; i < this.config.data.length; i++) {
                        let item = this.config.data[i]
                        let seriesValue = item[seriesProp]
                        let datasetIndex = this.seriesOption.length
                        let exist = false
                        for (let j = 0; j < this.seriesOption.length; j++) {
                            if (this.seriesOption[j] == seriesValue) {
                                exist = true
                                datasetIndex = j
                                break
                            }
                        }
                        if (!exist) {
                            this.seriesOption.push(seriesValue)
                            dataset.push([])
                        }
                        // indicator
                        let nameExist = false
                        let nameVal = item[nameProp]
                        for (let j = 0; j < indicat.length; j++) {
                            if (indicat[j]['name'] == nameVal) {
                                nameExist = true
                            }
                        }
                        if (!nameExist) {
                            indicat.push({
                                name: nameVal,
                                max: item[maxProp]
                            })
                        }
                        dataset[datasetIndex].push(item[valueProp])
                        if (item[valueProp] > max) {
                            max = item[valueProp]
                        }
                    }
                } else {
                    dataset = [[]]
                    for (let i = 0; i < this.config.data.length; i++) {
                        let item = this.config.data[i]
                        dataset[0].push(item[valueProp])
                        indicat.push({
                            name: item[nameProp],
                            max: item[maxProp]
                        })
                    }
                }
                this.dataset = dataset
                this.config.option.indicator = indicat
                // datasetseries
                if (!this.config.option.series || !this.config.option.series.length) {
                    return
                }
                for (let i = 0; i < this.config.option.series.length; i++) {
                    this.config.option.series[i].data = [this.dataset[i]]
                }
            }
        },
        watch: {
            'config.option.series': {
                handler: function (val, oldVal) {
                    if (!oldVal) {
                        return
                    }
                    this.handler()
                }
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
