<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseGraph',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'rgba(0,0,0,0)',
                    tooltip: {
                        show: true,
                        trigger: "item",
                        backgroundColor: '#2F3943',
                        borderColor: 'rgba(0,0,0,0)',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            fontSize: 12,
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
                    },
                    color: [
                        '#4CB7F2',
                        '#458FF0',
                        '#F5B751',
                        '#70C6A2',
                        '#70649A',
                        '#4F726C',
                        '#E58980',
                        '#BC9F77',
                        '#EDC7C7',
                        '#B55D4C',
                        '#69A8A0',
                        '#4CB7F2',
                        '#458FF0',
                        '#F5B751',
                        '#70C6A2',
                        '#B28ECC',
                        '#68A79F',
                        '#E58980',
                        '#BC9F77',
                        '#EDC7C7',
                        '#E19665',
                        '#8AA38A',
                        '#B28FCE',
                        '#6DA8C1',
                        '#A4809D',
                        '#7087B6',
                        '#AAB06A',
                        '#8AA38A',
                        '#D0A28F',
                        '#58AFA2',
                        '#6FBC89',
                    ],
                    legend: [{
                        show: true,
                        icon: "circle",
                        top: 'top',
                        left: 'left',
                        orient: "vertical",
                        itemWidth: 14,
                        itemHeight: 14,
                        textStyle: {
                            fontSize: 14,
                            color: "#fff"
                        },
                        data: null
                    }],
                    animationDurationUpdate: 1500,
                    animationEasingUpdate: 'quinticInOut',
                    series: [{
                        type: 'graph',
                        //  circular- / force-
                        layout: 'circular',
                        circular: {
                            rotateLabel: true
                        },
                        force: {
                            // 
                            repulsion: 100,
                        },
                        data: null,
                        links: null,
                        categories: null,
                        roam: true,
                        // 
                        focusNodeAdjacency: true,
                        height: 200,
                        width: 200,
                        label: {
                            normal: {
                                show: true,
                                fontSize: 12,
                                position: 'right',
                                formatter: '{b}'
                            }
                        },
                        lineStyle: {
                            normal: {
                                color: 'source',
                                curveness: 0.3,
                                width: 1,
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [{
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 24,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 24,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 24,
                    "targetValue": 18,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 24,
                    "targetValue": 24,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 18,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 18,
                    "targetValue": 15,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 18,
                    "targetValue": 15,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 12,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 12,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 12,
                    "targetValue": 21,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 12,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 12,
                    "targetValue": 6,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 12,
                    "targetValue": 6,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 6,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 6,
                    "targetValue": 21,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 9,
                    "targetValue": 15,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 9,
                    "targetValue": 12,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 9,
                    "targetValue": 24,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 9,
                    "targetValue": 9,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 9,
                    "targetValue": 21,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 3,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "60",
                    "sourceValue": 3,
                    "targetValue": 15,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 3,
                    "targetValue": 21,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 15,
                    "targetValue": 3,
                    "sourceCategory": "",
                    "targetCategory": ""
                }, {
                    "source": "",
                    "target": "",
                    "sourceValue": 3,
                    "targetValue": 21,
                    "sourceCategory": "",
                    "targetCategory": ""
                }]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'source', comment: '', prop: 'name', state: null},
                            {name: 'target', comment: '', prop: 'target', state: null},
                            {name: 'sourceValue', comment: '', prop: 'sourceValue', state: null},
                            {name: 'targetValue', comment: '', prop: 'targetValue', state: null},
                            {name: 'sourceCategory', comment: '', prop: 'sourceCategory', state: null},
                            {name: 'targetCategory', comment: '', prop: 'targetCategory', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        source: null,
                        target: null,
                        sourceValue: null,
                        targetValue: null,
                        sourceCategory: null,
                        targetCategory: null,
                    },
                    // 
                    variable: {
                        symbolSize: 1
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let sourceCategoryProp = this.config.transform.mapper.sourceCategory || 'sourceCategoryProp'
                let targetCategoryProp = this.config.transform.mapper.targetCategory || 'targetCategory'
                let sourceProp = this.config.transform.mapper.source || 'source'
                let targetProp = this.config.transform.mapper.target || 'target'
                let sourceValueProp = this.config.transform.mapper.sourceValue || 'sourceValue'
                let targetValueProp = this.config.transform.mapper.targetValue || 'targetValue'
                let links = []
                let nodeMap = {}
                let categoryMap = {}
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    links.push({
                        source: item[sourceProp],
                        target: item[targetProp]
                    })
                    categoryMap[item[sourceCategoryProp]] = item[sourceCategoryProp]
                    categoryMap[item[targetCategoryProp]] = item[targetCategoryProp]
                    let sourceKey = item[sourceProp]
                    let targetKey = item[targetProp]
                    if (!nodeMap[sourceKey]) {
                        nodeMap[sourceKey] = {
                            name: item[sourceProp],
                            symbolSize: item[sourceValueProp] * this.config.transform.variable.symbolSize,
                            value: item[sourceValueProp],
                            category: item[sourceCategoryProp]
                        }
                    }
                    if (!nodeMap[targetKey]) {
                        nodeMap[targetKey] = {
                            name: item[targetProp],
                            symbolSize: item[targetValueProp] * this.config.transform.variable.symbolSize,
                            value: item[targetValueProp],
                            category: item[targetCategoryProp]
                        }
                    }
                }
                let legend = []
                let categories = []
                for (let key in categoryMap) {
                    categories.push({name: categoryMap[key]})
                    legend.push(categoryMap[key])
                }
                let nodes = []
                for (let key in nodeMap) {
                    nodes.push(nodeMap[key])
                }
                this.config.option.series[0].data = nodes
                this.config.option.series[0].links = links
                this.config.option.series[0].categories = categories
                this.config.option.legend[0].data = legend
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
