                                            show-alpha></el-color-picker>
                                    <div class="description"></div>
                                </el-col>
                            </el-row>
                        </template>
                        <template v-if="series.type == 'line'">
                            <el-row>
                                <el-col :span="8" class="tag"></el-col>
                                <el-col :span="8">
                                    <el-checkbox v-model="component.config.option.series[i].smooth">
                                    </el-checkbox>
                                    <div class="description"></div>
                                </el-col>
                                <el-col :span="8">
                                    <el-color-picker v-model="component.config.option.series[i].itemStyle.normal.color"
                                                     size="mini"
                                                     show-alpha></el-color-picker>
                                    <div class="description"></div>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="8" class="tag"></el-col>
                                <el-col :span="16">
                                    <el-checkbox v-model="component.config.option.series[i].showSymbol">
                                    </el-checkbox>
                                </el-col>
                            </el-row>
                            <template v-if="component.config.option.series[i].showSymbol">
                                <el-row>
                                    <el-col :span="8" class="tag"></el-col>
                                    <el-col :span="8">
                                        <el-select v-model="component.config.option.series[i].symbol"
                                                   size="mini">
                                            <el-option
                                                    v-for="item in symbolType"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.series[i].symbolSize"
                                                size="mini"
                                                controls-position="right"
                                                :min="0"></el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-switch
                                                v-model="component.config.option.series[i].smooth">
                                        </el-switch>
                                    </el-col>
                                    <el-col :span="8" class="tag"></el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="8" class="tag"></el-col>
                                    <el-col :span="8">
                                        <el-input-number
                                                v-model="component.config.option.series[i].itemStyle.normal.label.fontSize"
                                                size="mini"
                                                controls-position="right"
                                                :min="12">
                                        </el-input-number>
                                        <div class="description"></div>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-color-picker
                                                v-model="component.config.option.series[i].itemStyle.normal.label.color"
                                                size="mini"
                                                show-alpha></el-color-picker>
                                        <div class="description"></div>
                                    </el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="8" class="tag"></el-col>
                                    <el-col :span="16">
                                        <el-select
                                                v-model="component.config.option.series[i].itemStyle.normal.label.position"
                                                placeholder=""
                                                size="mini">
                                            <el-option v-for="item in labelPosition"
                                                       :key="item.value"
                                                       :label="item.label"
                                                       :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                </el-row>
                            </template>
                        </template>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: 'StackSumBar',
        props: ['component', 'state'],
        data() {
            return {
                // 
                visibility: [
                    {label: '', value: 'visible'},
                    {label: '', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ],
                    legendX: 0
                },
                //
                symbolType: [{
                    value: 'emptyCircle',
                    label: ''
                }, {
                    value: 'circle',
                    label: ''
                }, {
                    value: 'rect',
                    label: ''
                }, {
                    value: 'roundRect',
                    label: ''
                }, {
                    value: 'triangle',
                    label: ''
                }, {
                    value: 'diamond',
                    label: ''
                }, {
                    value: 'pin',
                    label: ''
                }, {
                    value: 'arrow',
                    label: ''
                }],
                //
                labelPosition: [
                    {label: "", value: "top"},
                    {label: "", value: "left"},
                    {label: "", value: "right"},
                    {label: "", value: "bottom"},
                    {label: "", value: "inside"},
                    {label: "", value: "insideLeft"},
                    {label: "", value: "insideRight"},
                    {label: "", value: "insideTop"},
                    {label: "", value: "insideBottom"},
                    {label: "", value: "insideTopLeft"},
                    {label: "", value: "insideBottomLeft"},
                    {label: "", value: "insideTopRight"},
                    {label: "", value: "insideBottomRight"},
                ],
                //
                positionType: {
                    orient: [
                        {label: '', value: 'horizontal'},
                        {label: '', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '', value: 'left'},
                        {label: '', value: 'center'},
                        {label: '', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '', value: 'top'},
                        {label: '', value: 'middle'},
                        {label: '', value: 'bottom'},
                    ]
                },
                //
                fontWeight: [
                    {label: '', value: 'normal'},
                    {label: '', value: 'bold'},
                    {label: '', value: 'lighter'},
                ],
                areaColor: []
            }
        },
        created() {
            this.init()
        },
        computed: {},
        methods: {
            init() {
                this.legend.legendX = parseInt(this.component.config.option.legend.x);
            },
            /**
             * 
             */
            changeLengXPositon(val) {
                this.component.config.option.legend.x = val + '%';
            },
            /**
             * 
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper this.$set() 
                // mappersetterdom
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = '' + index
                this.component.config.option.legend.data.push(seriesName)
                let color = ColorUtils.randomHex()
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'bar',
                    barWidth: 20,
                    barGap: 1,
                    stack: index + '',
                    itemStyle: {
                        normal: {
                            color: {
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                type: "linear",
                                global: false,
                                colorStops: [{
                                    offset: 0,
                                    color: color
                                }, {
                                    offset: 1,
                                    color: color
                                }]
                            },
                        }
                    }
                })
            },
            /**
             * 
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // mapper
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.areaColor.splice(index, 1)
            },
            /**
             * series
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
