<template>
    <Plugin :config="config" :base="base">
        <div class="scroll-list">
            <transition-group tag="ul" :style="calcStyle()" name="scroll-up">
                <li v-for="(item,i) in config.option.data"
                    v-show="isVisible(i)"
                    :style="calcItemStyle()"
                    :key='i'
                    @click="onEvent('click', item)">
                    <div>{{item.name}}</div>
                </li>
            </transition-group>
        </div>
    </Plugin>
</template>

<script>
    import '../../../../assets/styles/common/paint/scroll-list.scss'
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'ScrollList',
        props: ['config', 'base'],
        data() {
            return {
                // 
                thread: null,
                // 
                index: 0,
                // 
                oldDelay: 5
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 
             */
            isVisible(i) {
                if (i >= this.index && i < this.index + this.config.option.count) {
                    return true
                }
                return false
            },
            /**
             * 
             */
            calcStyle() {
                return {
                    height: this.config.style.itemHeight * this.config.option.count + 'px',
                    opacity: this.config.style.opacity,
                    color: this.config.style.color,
                    fontSize: this.config.style.fontSize + 'px',
                }
            },
            /**
             * 
             */
            calcItemStyle() {
                return {
                    width: this.config.style.itemWidth + 'px',
                    height: this.config.style.itemHeight + 'px',
                    lineHeight: this.config.style.itemHeight + 'px',
                }
            },
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 200,
                    height: 120,
                    itemWidth: 200,
                    itemHeight: 40,
                    opacity: 1,
                    color: '#00BAFF',
                    fontSize: 20
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '', value: 0},
                    {name: '', value: 1},
                    {name: '', value: 2},
                    {name: '', value: 3},
                    {name: '', value: 3},
                    {name: '', value: 3},
                    {name: '', value: 3},
                    {name: '', value: 3},
                ]
            },
            /**
             * option
             */
            initOption() {
                this.config.option = this.config.option || {
                    count: 3,
                    delayTime: 5,
                    data: []
                }
                this.oldDelay = this.config.option.delayTime
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '',
                    binders: [
                        {name: 'name', comment: '', prop: 'name', state: null},
                        {name: 'value', comment: '', prop: 'value', state: null},
                    ]
                }]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
                this.stop()
                this.play()
            },
            /**
             * 
             */
            onEvent(eventName, item) {
                this.$children[0].onEvent(eventName, item)
            },
            /**
             * 
             */
            play() {
                this.thread = setInterval(() => {
                    if (this.oldDelay !== this.config.option.delayTime) {
                        this.stop()
                        this.oldDelay = this.config.option.delayTime
                        this.play()
                    }
                    this.scrollList()
                }, this.config.option.delayTime * 1000)
            },
            /**
             * 
             */
            scrollList() {
                if (!this.config.option.data || !this.config.option.data.length || this.config.option.data.length <= this.config.option.count) {
                    return
                }
                this.index += this.config.option.count
                if (this.index >= this.config.option.data.length) {
                    this.index = 0
                }
            },
            /**
             * 
             */
            stop() {
                clearInterval(this.thread)
            }
        },
        destroyed() {
            this.stop()
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
